// goo-fish/src/components/Navbar/Navbar.js
import React from 'react';
import './Navbar-Right-Links-icons.css'; // Подключаем CSS

// Импортируем изображения
import logo from './img/logo.png';
import searchIcon from './img/asd.png';
import loginIcon from './img/asc.png';
import ordersIcon from './img/asb.png';

const Navbar = () => {
    return (
        <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" id="headerNav" style={{ width: '100%', background: '#ffe60f', maxHeight: '72px' }}>
            <div className="container">
                <div className="d-flex align-items-center align-items-xxl-center" style={{ width: '100%', height: '100%', maxHeight: '72px' }}>
                    <div style={{ width: '100%', height: '100%' }}>
                        <img src={logo}  style={{ height: '100%', maxHeight: '72px' }} alt="Logo" />
                    </div>
                    <div className="mobile_drop" style={{ width: '100%' }}>
                        <div className="d-inline-flex d-xl-flex align-items-center align-items-md-center align-items-xxl-center" style={{ maxWidth: '450px', width: '100%' }}>
                            <input type="text" style={{ width: '100%', height: '39px', borderRadius: '30px', paddingLeft: '20px', borderWidth: '1px', borderStyle: 'solid', outline: 'none' }} placeholder="老爹鞋" />
                            <button className="btn text-nowrap d-inline-flex align-items-center align-items-md-center align-items-lg-center align-items-xl-center" type="button" style={{ background: 'rgb(255, 230, 15)', color: 'rgb(0,0,0)', borderRadius: '20px', border: '1px none rgb(0,0,0)', marginLeft: '-78px', maxWidth: '76px', width: '100%', height: '35px' }}>
                                <img src={searchIcon} style={{ width: '16px' }} alt="Search" />&nbsp;搜索
                            </button>
                        </div>
                    </div>
                    <div className="text-nowrap text-end d-flex justify-content-end align-items-center" style={{ width: '100%' }}>
                        <a className="active text-decoration-none d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center text-black-href" href="#" style={{ marginRight: '5px' }}>
                            <img src={loginIcon} style={{ height: '24px' }} alt="Login" />&nbsp;登录
                        </a>
                        <a className="text-decoration-none d-flex d-sm-flex align-items-center justify-content-sm-center align-items-sm-center active text-black-href" href="#" style={{ marginLeft: '5px' }}>
                            <img src={ordersIcon} style={{ height: '24px' }} alt="Orders" />&nbsp;订单
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Navbar;