// src/components/Home.js
import React, { useRef } from 'react';
import Navbar from './components/Navbar/Navbar';
import IconProfile from './components/IconProfile/IconProfile';
import MainBanner from './components/MainBanner/MainBanner';
import ChooseMethod from './components/ChooseMethod/ChooseMethod';
import Footer from './components/Footer/Footer';

const Home = ({ handleGoToChoose, showChooseMethod, chooseMethodRef }) => {
  return (
    <div className="App">
      <Navbar />
      <IconProfile />
      <MainBanner onGoToChoose={handleGoToChoose} />
      {showChooseMethod && (
        <div ref={chooseMethodRef}>
          <ChooseMethod />
        </div>
      )}
      <Footer />
    </div>
  );
};

export default Home;