// src/components/IconProfile/IconProfile.js
import React, { useEffect, useState } from 'react';
import profileImage from './img/iconprofile_.avif'; // Импортируем изображение
import svgImage from './img/svgviewer-png-output.png'; // Импортируем второе изображение
import { fetchAdvertById } from '../../functions/api'; // Импортируем функцию для получения данных

const IconProfile = () => {
    const [advertData, setAdvertData] = useState(null);

    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const advertId = params.get('advertId');

        if (advertId) {
            fetchAdvertById(advertId)
                .then(data => {
                    setAdvertData(data);
                })
                .catch(error => {
                    console.error('Ошибка при загрузке данных:', error);
                })
                .finally(() => {
                    
                });
        } else {
        }
    }, []);

    return (
        <section style={{ marginTop: '30px' }}>
            <div className="container">
                <div>
                    <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%', background: '#ffffff', borderRadius: '20px', borderStyle: 'none', padding: '0 20px', height: '72px' }}>
                        <div className="d-flex d-xxl-flex justify-content-start align-items-center justify-content-xxl-start align-items-xxl-center">
                            <img src={advertData ? advertData.sellerAvatarUrl : profileImage} id='sellerAvatarUrl' style={{ width: '40px', borderRadius: '81px', border: '2px solid rgba(0,0,0,0.1)' }} alt="Profile" />
                        </div>
                        <div style={{ width: '100%', marginLeft: '20px' }}>
                            <div style={{ width: '100%', fontSize: '14px' }}>
                                <span style={{ fontSize: '14px' }} id='sellerNickname'>{advertData ? advertData.sellerNickname : ''}</span>
                            </div>
                            <div className="d-sm-flex d-xl-flex align-items-sm-center align-items-xl-center mobile_drop" style={{ width: '100%' }}>
                                <span style={{ fontSize: '12px' }}>
                                    <a className="text-decoration-none" href="#" style={{ color: 'rgb(102, 102, 102)', fontSize: '12px' }} id='city'>{advertData ? advertData.city : ''}</a>&nbsp;
                                    <span style={{ color: 'rgba(0, 0, 0, 0.08)' }}>|</span>&nbsp;
                                    <a className="text-decoration-none" href="#" style={{ color: 'rgb(102, 102, 102)', fontSize: '12px' }} id='lastOnline'>{advertData ? `${advertData.lastOnline}` : ''}</a>&nbsp;
                                    <span style={{ color: 'rgba(0, 0, 0, 0.08)' }}>|</span>&nbsp;
                                    <a className="text-decoration-none" href="#" style={{ color: 'rgb(102, 102, 102)', fontSize: '12px' }} id='additionalInfo'>{advertData ? advertData.additionalInfo : ''}</a>&nbsp;
                                    <span style={{ color: 'rgba(0, 0, 0, 0.08)' }}>|</span>&nbsp;
                                    <a className="text-decoration-none" href="#" style={{ color: 'rgb(102, 102, 102)', fontSize: '12px' }} id='salesCount'>{advertData ? `${advertData.salesCount}` : ''}</a>&nbsp;
                                    <span style={{ color: 'rgba(0, 0, 0, 0.08)' }}>|</span>&nbsp;
                                    <a className="text-decoration-none" href="#" style={{ color: 'rgb(102, 102, 102)', fontSize: '12px' }} id='sellerRating'>{advertData ? advertData.sellerRating : ''}</a>&nbsp;
                                </span>
                            
                            </div>
                        </div>
                        <div className="d-flex d-xxl-flex justify-content-end align-items-center justify-content-xxl-end align-items-xxl-center" style={{ width: 'auto' }}>
                            <a className="btn text-nowrap d-flex d-xxl-flex align-items-center align-items-xxl-center" role="button" style={{ background: 'rgb(246, 246, 246)', borderRadius: '30px', fontSize: '14px', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)' }} href="#" target="_blank" rel="noreferrer">
                                <img src={svgImage} alt="Goofish" />&nbsp;闲鱼号
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default IconProfile;