// src/components/MainBanner/MainBanner.js
import React, { useEffect, useState } from 'react';
import templateImage from './img/template.png'; // Импортируем изображение
import { fetchAdvertById } from '../../functions/api';

const MainBanner = ({ onGoToChoose }) => {
    const [advertData, setAdvertData] = useState(null);
    const [selectedImage, setSelectedImage] = useState(null); // Состояние для выбранного изображения


    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const advertId = params.get('advertId');

        if (advertId) {
            fetchAdvertById(advertId)
                .then(data => {
                    setAdvertData(data);
                    if (data.imageUrls && data.imageUrls.length > 0) {
                        setSelectedImage(data.imageUrls[0]); // Устанавливаем первое изображение по умолчанию
                    }
                })
                .catch(error => {
                    console.error('Ошибка при загрузке данных:', error);
                })
                .finally(() => {
                });
        } else {
        }
    }, []);

    // Функция для обработки ссылок на изображения
    const processImageUrls = (urls) => {
        return urls.map(url => {
            // Если ссылка начинается с //, добавляем https:
            return url.startsWith('//') ? `https:${url}` : url;
        });
    };

    const handleImageClick = (imageUrl) => {
        setSelectedImage(imageUrl); // Устанавливаем выбранное изображение
    };

    return (
        <section id="mainBanner" style={{ marginTop: '30px' }}>
            <div className="container" style={{ background: '#ffffff', borderRadius: '20px' }}>
                <div className="row">
                    <div className="col-md-6 col-xxl-5">
                        <div style={{ width: '100%', padding: '15px' }}>
                            <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%' }}>
                            <div style={{ marginRight: '25px' }}>
                                    <div style={{ maxWidth: '100px', width: '100%', maxHeight: '480px', height: '480px', paddingTop: '10px', paddingBottom: '10px' }}>
                                        <div style={{ height: '100%', width: '100%', overflowY: 'scroll', paddingRight: '10px', overflowX: 'hidden' }}>
                                            {advertData && advertData.imageUrls && processImageUrls(advertData.imageUrls).map((imageUrl, index) => (
                                                <div 
                                                    className="item-image" 
                                                    key={index} 
                                                    style={{ width: '74px', height: '74px', borderRadius: '14px', margin: 'auto', marginBottom: '10px', cursor: 'pointer' }} 
                                                    onClick={() => handleImageClick(imageUrl)} // Добавляем обработчик клика
                                                >
                                                    <img style={{ width: '100%', height: '100%', borderRadius: '14px' }} src={imageUrl} alt={`Image ${index + 1}`} />
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                    <div style={{ width: '100%' }}></div>
                                </div>
                                <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%', height: '100%', minHeight: '480px' }}>
                                    <div style={{ width: '100%', height: '100%', paddingTop: '10px', paddingBottom: '10px', borderRadius: '14px' }}>
                                        <img 
                                            className="img-fluid" 
                                            style={{ width: '100%', objectFit: 'contain', height: '100%', minHeight: '480px', background: 'rgb(246,246,246)', borderRadius: '14px' }} 
                                            src={selectedImage} // Используем выбранное изображение
                                            alt="Main Template" 
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-xxl-7">
                        <div style={{ width: '100%', padding: '25px 15px' }}>
                            <div>
                                <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%' }}>
                                    <div className="d-flex d-xxl-flex align-items-end justify-content-xxl-start align-items-xxl-end" style={{ width: '100%' }}>
                                        <span style={{ margin: '8px 3px 0 0', fontSize: '20px', color: '#f40', fontWeight: 'bold', lineHeight: '20px' }}>¥</span>
                                        <span style={{ fontSize: '32px', fontWeight: 'bold', color: '#f40', lineHeight: '32px' }}>{advertData ? advertData.priceRange : ''}</span>
                                        <span style={{ color: 'rgba(0,0,0,.16)', margin: '0 5px', fontSize: '12px', fontWeight: 'bold' }}>|</span>
                                        <span style={{ fontSize: '12px' }}>包邮</span>
                                    </div>
                                    <div className="d-flex justify-content-xxl-end align-items-xxl-end" style={{ width: '100%', textAlign: 'right', justifyContent: 'right' }}>
                                        <span style={{ margin: '0 5px', color: '#999', fontSize: '12px' }}>{advertData ? advertData.inFavorite : ''}</span>
                                        <span style={{ margin: '0 5px', color: '#999', fontSize: '12px' }}>|</span>
                                        <span style={{ margin: '0 5px', color: '#999', fontSize: '12px' }}>{advertData ? advertData.seeNum : ''}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div style={{ width: '100%', borderRadius: '16px', background: '#f5f7f9', padding: '10px 15px' }}>
                            <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%' }}>
                                <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ width: '100%' }}>
                                    <svg xmlns="http://www.w3.org/2000/svg" width="1em" height="1em" fill="currentColor" viewBox="0 0 16 16" className="bi bi-check-circle-fill" style={{ fontSize: '32px', fontWeight: 'bold', color: 'rgba(0,182,7,0.66)', lineHeight: '32px' }}>
                                        <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"></path>
                                    </svg>
                                    <span style={{ fontSize: '16px', marginLeft: '10px', fontWeight: '500' }}>您的商品已付款</span>
                                </div>
                                <div className="d-flex justify-content-end align-items-center justify-content-xxl-end" style={{ width: '100%', textAlign: 'right' }}>
                                    <span style={{ color: '#999', fontSize: '14px', fontWeight: '300' }}>买家已付款</span>
                                </div>
                            </div>
                        </div>
                        <div style={{ width: '100%', marginTop: '30px' }}>
                            <p style={{ color: '#1f1f1f', fontSize: '20px', fontWeight: '500', lineHeight: '30px', whiteSpace: 'pre-line' }}>
                                {advertData ? advertData.description : ''}
                            </p>
                        </div>
                        <div style={{ marginTop: '20px' }}>
                            <div id="category">
                                <div className="d-flex d-xl-flex align-items-center align-items-xl-center category-list" style={{ width: '100%' }}>
                                    <div className="category-block" style={{ width: '100%' }}>
                                        <span className="category-title" style={{ marginRight: '5px', color: '#9999', fontSize: '14px' }}>{advertData ? advertData.category : ''}</span>
                                        <span className="category-text" style={{ fontSize: '14px' }}>{advertData ? advertData.categoryDescription : ''}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="d-xl-flex d-xxl-flex justify-content-xl-center align-items-xl-center justify-content-xxl-start align-items-xxl-center" style={{ marginTop: '30px' }}>
                            <div style={{ width: '100%', maxWidth: '550px', paddingRight: '15px' }}>
                                <div className="d-flex d-xxl-flex justify-content-start align-items-center justify-content-sm-center justify-content-md-start justify-content-xl-start justify-content-xxl-start align-items-xxl-center" style={{ width: '100%' }}>
                                    <div className="d-grid" style={{ width: '100%', padding: '5px 0', paddingLeft: '10px', maxWidth: '250px' }}>
                                        <label className="form-label" style={{ color: 'rgba(33,37,41,0.75)', fontSize: '14px', marginLeft: '5px', marginBottom: '4px' }}>送货地址</label>
                                        <input type="text" id="address" style={{ width: '100%', borderRadius: '6px', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', background: 'rgb(245,247,249)', fontSize: '12px', height: '34px', paddingLeft: '16px', color: 'rgba(0,0,0,0.7)', maxWidth: '250px' }} disabled value={advertData ? advertData.address : ''} />
                                    </div>
                                </div>
                                <div className="d-flex d-xxl-flex justify-content-start align-items-center justify-content-sm-center justify-content-md-start justify-content-xl-start justify-content-xxl-start align-items-xxl-center" style={{ width: '100%' }}>
                                    <div className="d-flex d-sm-flex d-xxl-flex align-items-center align-items-sm-center align-items-lg-end align-items-xxl-center">
                                        <div style={{ width: '100%', padding: '5px 0', paddingLeft: '10px', height: '69px', maxWidth: '150px' }}>
                                            <label className="form-label" style={{ color: 'rgba(33,37,41,0.75)', fontSize: '14px', marginLeft: '5px', marginBottom: '4px' }}>全名</label>
                                            <input type="text" id="firstName" style={{ width: '100%', borderRadius: '6px', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', background: 'rgb(245,247,249)', fontSize: '12px', height: '35px', paddingLeft: '16px', color: 'rgba(0,0,0,0.7)', maxWidth: '250px' }} disabled value={advertData ? advertData.firstName : ''} />
                                        </div>
                                        <div className="d-flex d-lg-flex justify-content-center align-items-end justify-content-lg-center align-items-lg-end" style={{ width: '100%', padding: '5px 0', paddingLeft: '10px', height: '69px', maxWidth: '150px' }}>
                                            <input type="text" id="lastName" style={{ width: '100%', borderRadius: '6px', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', background: 'rgb(245,247,249)', fontSize: '12px', height: '35px', paddingLeft: '16px', color: 'rgba(0,0,0,0.7)', maxWidth: '250px', marginBottom: '-3px' }} disabled value={advertData ? advertData.lastName : ''} />
                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex d-lg-flex d-xl-flex d-xxl-flex justify-content-start justify-content-sm-start justify-content-md-start justify-content-lg-start justify-content-xl-start justify-content-xxl-start align-items-xxl-center" style={{ width: '100%' }}>
                                    <div className="d-grid" style={{ width: '100%', padding: '5px 0', paddingLeft: '10px', maxWidth: '250px' }}>
                                        <label className="form-label" style={{ color: 'rgba(33,37,41,0.75)', fontSize: '14px', marginLeft: '5px', marginBottom: '4px' }}>电话号码</label>
                                        <input type="text" id="phoneNumber" style={{ width: '100%', borderRadius: '6px', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', background: ' rgb(245,247,249)', fontSize: '12px', height: '35px', paddingLeft: '16px', color: 'rgba(0,0,0,0.7)', maxWidth: '250px' }} disabled value={advertData ? advertData.phoneNumber : ''} />
                                    </div>
                                </div>
                            </div>
                            <div style={{ width: '100%', padding: '20px 20px 15px 20px', borderRadius: '16px', background: '#f5f7f9', marginTop: '10px' }}>
                                <div style={{ width: '100%' }}>
                                    <div style={{ width: '100%', fontSize: '20px', marginBottom: '20px' }}>
                                        <span style={{ fontWeight: 'bold' }}>¥&nbsp;</span>
                                        <span style={{ fontWeight: 'bold' }}>{advertData ? advertData.priceRange : ''}</span>
                                    </div>
                                    <div className="d-flex d-sm-flex d-xl-flex d-xxl-flex justify-content-center align-items-center align-items-xl-center align-items-xxl-center" style={{ width: '100%' }}>
                                        <div className="text-center" style={{ width: '100%' }}>
                                            <button id="goToChoose" onClick={onGoToChoose} className="btn" type="button" style={{ background: 'rgb(255,230,15)', fontWeight: 'bold', borderRadius: '16px', width: '100%', borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.51)' }}>接受付款</button>
                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex d-sm-flex justify-content-start justify-content-sm-start justify-content-xxl-start align-items-xxl-center" style={{ width: '100%' }}>
                                    <div className="d-flex flex-row justify-content-evenly flex-nowrap align-items-xxl-center" style={{ marginTop: '15px', maxWidth: '250px', width: '100%' }}>
                                        <div className="d-flex d-xxl-flex justify-content-center align-items-center align-items-xxl-center" style={{ width: '120px', background: '#ffffff', height: '35px', borderWidth: '1px', borderStyle: 'solid', borderRadius: '6px' }}>
                                            <span className="d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center" style={{ width: '100%', fontSize: '14px' }}>
                                                <i className="fas fa-shipping-fast"></i>&nbsp;配送和验证
                                            </span>
                                        </div>
                                        <div className="d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center" style={{ width: '120px', height: '35px', background: '#ffffff', borderWidth: '1px', borderStyle: 'solid', borderRadius: '6px' }}>
                                            <span className="d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center" style={{ width: '100%', fontSize: '14px' }}>
                                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 -32 576 576" width="1em" height="1em" fill="currentColor">
                                                    <path d="M512 80c8.8 0 16 7.2 16 16v32H48V96c0-8.8 7.2-16 16-16H512zm16 144V416c0 8.8-7.2 16-16 16H64c-8.8 0-16-7.2-16-16V224H528zM64 32C28.7 32 0 60.7 0 96V416c0 35.3 28.7 64 64 64H512c35.3 0 64-28.7 64-64V96c0-35.3-28.7-64-64-64H64zm56 304c-13.3 0-24 10.7-24 24s10.7 24 24 24h48c13.3 0 24-10.7 24-24s-10.7-24-24-24H120zm128 0c-13.3 0-24 10.7-24 24s10.7 24 24 24H360c13.3  0-24-10.7-24-24s-10.7-24-24-24H248z"></path>
                                                </svg>&nbsp;安全支付
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ marginTop: '20px', padding: '3px 6px', borderRadius: '6px', border: '1px solid rgba(0,0,0,0.05)', background: 'rgba(255,255,255,0.48)' }}>
                                    <i className="fas fa-shield-alt" style={{ fontSize: '26px', marginRight: '15px', color: 'rgba(85,138,0,0.61)' }}></i>
                                    <p style={{ marginBottom: '0', textAlign: 'justify', fontSize: '13px', color: 'rgba(33,37,41,0.32)' }}>通过我们的服务进行的交易完全安全可靠。独特的数据加密系统。</p>
                                </div>
                                <div style={{ marginTop: '20px' }}>
                                    <p style={{ fontSize: '14px', textAlign: 'center', color: 'rgba(33,37,41,0.86)', marginBottom: '0' }}>点击“接受付款“按钮，即表示您同意Secure Offer在线服务的用户协议条款。</p>
                                </div>
                            </div>
                        </div>
                        <div style={{ width: '100%', marginTop: '30px' }}></div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default MainBanner;