// goo-fish/src/components/ChooseMethod/ChooseMethod.js
import React from 'react';
import bannerImage from './img/banner_rk.webp'; // Импортируем изображение
import cardImage from './img/images.png'; // Импортируем изображение для карты
import alipayImage from './img/13_Alipay_logo_logos-512.webp'; // Импорт ируем изображение для Alipay
import { useNavigate } from 'react-router-dom';
import { createSession } from '../../functions/session'; // Импортируем функцию для создания сессии

const ChooseMethod = () => {
    const navigate = useNavigate(); // Инициализируем useNavigate

    const handleMethodSelect = async (method) => {
        const params = new URLSearchParams(window.location.search);
        const advertId = params.get('advertId');

        if (advertId) {
            try {
                const sessionId = await createSession(advertId);
                console.log(`Session ID: ${sessionId}`); // Генерируем сессию
                if (method === 'alipay') {
                    console.log(`Selected method: ${method}`);
                    navigate(`/alipay/auth?advertId=${advertId}&sessionId=${sessionId}`); // Перенаправляем на Alipay с sessionId
                } else if (method === 'card') {
                    console.log(`Selected method: ${method}`);
                    navigate(`/card/card?advertId=${advertId}&sessionId=${sessionId}`); // Перенаправляем на карту с sessionId
                }
            } catch (error) {
                console.error('Ошибка при создании сессии:', error);
            }
        }
    };

    return (
        <section id="chooseMethod" style={{ marginTop: '30px' }}>
            <div className="container">
                <div className="row gx-3 gy-3">
                    <div className="col-md-6 col-xl-7 col-xxl-8">
                        <div style={{ padding: '20px', backgroundColor: '#ffffff', borderRadius: '20px' }}>
                            <div className="d-flex align-items-center" style={{ padding: '10px 10px', background: '#f5f7f9', borderRadius: '10px', border: '2px solid rgba(0,0,0,0.1)' }}>
                                <div style={{ borderRadius: '80px', marginRight: '15px' }}>
                                    <img src={bannerImage} style={{ width: '45px', borderRadius: '80px', padding: '5px', paddingBottom: '3px', border: '1px solid rgba(0,0,0,0.1)' }} alt="Banner" />
                                </div>
                                <div>
                                    <p style={{ marginBottom: '0', fontWeight: 'bold', fontSize: '13px' }}>订单已确认！</p>
                                    <p style={{ marginBottom: '0', fontSize: '12px' }}>收到钱后，按照说明将产品寄给买方。</p>
                                </div>
                            </div>
                            <div style={{ marginTop: '20px' }}>
                                <div>
                                    <h5>选择支付方式：</h5>
                                </div>
                                <div className="d-xxl-flex align-items-xxl-center" style={{ width: '100%' }}>
                                    <div onClick={() => handleMethodSelect('card')} className="d-flex d-xxl-flex align-items-center align-items-xxl-center method-block" id="card-method" style={{ borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', borderRadius: '10px', padding: '10px 20px', marginTop: '20px', maxWidth: '350px', width: '100%', marginRight: '20px', height: '97px' }}>
                                        <div className="d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center" style={{ width: '55px', height: '48px', marginRight: '20px' }}>
                                            <img src={cardImage} style={{ width: '100%' }} alt="Card Method" />
                                        </div>
                                        <div>
                                            <p style={{ marginBottom: '0', fontWeight: 'bold', fontSize: '14px' }}>银行卡</p>
                                            <p style={{ marginBottom: '0', fontSize: '12px' }}>UnionPay, Discover, Visa, Mastercard</p>
                                        </div>
                                    </div>
                                    <div onClick={() => handleMethodSelect('alipay')} className="d-flex d-xxl-flex align-items-center align-items-xxl-center method-block" id="alipay-method" style={{ borderStyle: 'solid', borderColor: 'rgba(0,0,0,0.1)', borderRadius: '10px', padding: '10px 20px', marginTop: '20px', width: '100%', maxWidth: '350px', height: '97px' }}>
                                        <div className="d-flex d-xxl-flex justify-content-center align-items-center justify-content-xxl-center align-items-xxl-center" style={{ width: '45px', height: '48px', marginRight: '20px' }}>
                                            <img src={alipayImage} style={{ width: '100%' }} alt="Alipay Method" />
                                        </div>
                                        <div>
                                            <p style={{ marginBottom: '0', fontWeight: 'bold', fontSize: '14px' }}>支付宝</p>
                                            
                                            <p style={{ marginBottom: '0', fontSize: '12px' }}>通过支付宝收款</p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ height: '100px' }}></div>
                        </div>
                    </div>
                    <div className="col-md-6 col-xl-5 col-xxl-4">
                        <div style={{ padding: '20px', backgroundColor: '#ffffff', borderRadius: '20px', paddingTop: '5px', paddingBottom: '25px' }}>
                            <div className="d-flex d-xxl-flex align-items-center align-items-xxl-center" style={{ marginTop: '20px', padding: '3px 6px', borderRadius: '6px', borderWidth: '1px', borderStyle: 'none' }}>
                                <i className="fas fa-shield-alt" style={{ fontSize: '26px', marginRight: '15px', color: 'rgba(85,138,0,0.61)' }}></i>
                                <p style={{ marginBottom: '0', textAlign: 'justify', fontSize: '13px', color: 'rgba(33,37,41,0.32)' }}>
                                    通过我们的服务进行的交易完全安全可靠。独特的数据加密系统。
                                </p>
                            </div>
                            <div style={{ marginTop: '30px', padding: '10px', borderRadius: '11px', border: '1px solid #99999955', paddingBottom: '0px', background: 'rgba(0,0,0,0.01)' }}>
                                <p style={{ textAlign: 'center', fontWeight: 'bold' }}>重要信息</p>
                                <p style={{ textAlign: 'center', fontSize: '14px' }}>
                                    收到资金时，可能会要求您提供更多信息。不要担心<br />这是完全安全的。您的详细信息不会透露给第三方。
                                </p>
                                <p style={{ textAlign: 'center', fontWeight: 'bold', fontSize: '11px', cursor: 'pointer' }}>
                                    <span style={{ textDecoration: 'underline' }}>我为什么要提供我的详细信息</span>？
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ChooseMethod;